import React, { useState } from 'react';
import moment from 'moment';

export const MonthView = () => {
  return (
    <div className="">
      <div className="big-year">{moment.currentYear()}</div>
      <div className="months">
        {moment.months().map((month, index) => (
          <span key={month} id={index + 1} className="each-month">
            {month}{' '}
          </span>
        ))}
      </div>
      <hr className="month-line"></hr>
      <table className="">
        <thead>
          <tr>
            {moment.weekdays().map(weekDay => <th class="days-of-week">Sun</th>)}
          </tr>
        </thead>
        <tbody id="calendar-body"></tbody>
      </table>
    </div>
  );
};
